.infoBox {
    flex: 1;
    cursor: pointer;
}

.infoBox:not(:last-child) {
    margin-right: 10px;
}

.cases.infoBox--selected{
    border-top: 10px solid rgb(251, 68, 67);;
}

.recovered.infoBox--selected{
    border-top: 10px solid greenyellow;
}

.deaths.infoBox--selected{
    border-top: 10px solid #fb4443;
}

.infoBox__cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox__total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}

.recovered.infoBox__cases{
    color: greenyellow !important;
}